.header{
  width: 100%;
  background: #fff;
  box-shadow: 0 8px 28px 0 rgba(72, 89, 102, 0.1);
  height:  $headerHeight;
  padding: 0 45px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 102;
  .header-center{
    @include clearfix();
    .logo{
      display: inline-block;
      padding-top: 11px;
    }
    .notification{
      float: right;
      padding: 20px 30px 0 0;
      position: relative;
      a{
        font-size: 30px;
        color:$companyColor;
        position: relative;
        span{
          @include position(absolute, 5px 0px null null);
          font-size: 8px;
          border: 1px solid $companyColor;
          @include border-radius(50%);
          width: 12px;
          height: 12px;
          text-align: center;
          display: inline-block;
          line-height: 12px;
          background: #fff;
        }
        i{
          display: inline-block;
          &:before{
            display: block;
          }
        }
      }
    }
    .notification-list{
      width: 340px;
      height: 525px;
      @include position(absolute, $headerHeight null null -45px);
      display: none;
      &.show{
        display: block;
      }
      &:after{
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 10px 10px 10px;
        border-color: transparent transparent $companyColor transparent;
        content: "";
        display: inline-block;
        @include position(absolute, -9px null null 50px);
      }
      .title{
        line-height: 45px;
        text-align: center;
        height: 45px;
        background-color: #ddbb74;
        color:#fff;
        font-size: 16px;
        @include border-radius(4px 4px 0 0);
      }
      .center{
        background: #f6f6f6;
        @include border-radius(4px);
        border: 1px solid #e6eaee;
        box-shadow: 0 0 10px 0 rgba(197, 205, 215, 0.76);
        overflow-y:scroll;
        height: 100%;

        .items-list{
          padding: 0 10px;
          .items{
            padding: 20px 0;
            border-bottom: 1px solid #c5cdd7;
            @include clearfix();
            position: relative;
            @include display(flex);
            align-items: center;
            -webkit-align-items: center;
            .logos{
              width: 40px;
              height: 40px;
              @include border-radius(50%);
              overflow: hidden;
              border: 1px solid #c5cdd7;
              display: inline-block;
              vertical-align: middle;
              background: #fff;
              img{
                max-width: 100%;
                max-height: 100%;
              }
            }
            .text{
              font-family: $sourceSansFontFamily;
              font-size: 16px;
              color:$textColorLight;
              padding: 0 18px;
              display: inline-block;
              vertical-align: middle;
              @include flex(1);
            }
            a{
              color:$companyColor;
              text-decoration: underline;
              font-size: 16px;
              &.delete-notification{
                @include position(absolute, 50% 0px null null);
                margin-top: -9px;
                font-size: 18px;
              }
            }
            &:last-child{
              border-bottom: 0;
            }
          }
        }
      }
    }
    .search{
      float: right;
      max-width: 240px;
      width: 100%;
      position: relative;
      margin-top: 20px;
      .txt-search{
        width: 100%;
        height: 30px;
        line-height: 31px;
        @include border-radius(15px);
        background-color: #f8f9fb;
        border: 1px solid $companyColor;
        font-size: 11px;
        color: #1f263e;
        @include placeholder(0.25);
        padding: 0 10px 0 40px;
      }
      .btn-search{
        background: transparent;
        border: 0;
        outline: none;
        @include position(absolute,0px null null 0px);
        i{
          font-size: 18px;
        }
      }
    }
  }
}
.modal{
  &.notification-result{
    padding: 30px;
    max-width: 840px;
    .close-modal{
      top: 10px;
      right: 10px;
      background-image: url('data:image/png;base64,');
    }
    strong{
      font-size: 25px;
      font-weight: normal;
      color:$companyColor;
    }
  }
}